<template>
  <div class="col s12 m4 l3">
    <div class="card small sticky-action">
      <div class="card-image waves-effect waves-block waves-light">
        <img class="activator" :src="imageFile" @click="hideContactPic">
      </div>
      <div class="card-content">
        <div v-if="contactPic !== null"
          :style="`background-image: url('${contactPic}');opacity:${hidePic ? '0' : '1'};`"
          class="contact-pic"
        ></div>
        <span class="card-title activator grey-text text-darken-4" @click="hideContactPic">{{ title }}<i class="material-icons right">more_vert</i></span>
        <p>{{ nom }}</p>
      </div>
      <div class="card-action">
        <!-- <a v-if="acceptable" @click.prevent="accepter">Accepter</a> -->
        <a v-if="numero" @click.prevent="appeler">Appeler</a>
        <a v-if="supprimable" @click.prevent="supprimer">Supprimer</a>
      </div>
      <div class="card-reveal">
        <span class="card-title grey-text text-darken-4">{{ title }}<i class="material-icons right" @click="showContactPic">close</i></span>
        <p>
          {{ nom }}<br>
          {{ details }}
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Sos',
  props: {
    acceptable: {
      default: true
    },
    supprimable: {
      default: true
    },
    numero: {
      default: '06 04 16 59 59'
    },
    details: {
      default: 'Default description'
    },
    type: {
      default: 'default'
    },
    nom: {
      default: 'Unspecified person'
    },
    fini: {
      default: false
    },
    contactId: {
      default: null
    },
    id: {
      default: null
    },
    contactPic: {
      default: null
    }
  },
  data () {
    return {
      hidePic: false
    }
  },
  computed: {
    imageFile () {
      return require('./../../assets/img/' + this.type + '.jpg')
    },
    title () {
      return 'SOS ' + this.type
    }
  },
  methods: {
    hideContactPic () {
      this.hidePic = true
    },
    showContactPic () {
      this.hidePic = false
    },
    accepter () {
      this.$emit('accepter')
    },
    supprimer () {
      this.$emit('supprimer')
    },
    appeler () {
      alert('Appel du ' + this.numero + ' non implment.')
    }
  }
}
</script>

<style scoped>
.contact-pic{
  border-radius: 100%;
  top: 20px;
  width: 75px;
  height: 75px;
  z-index: 2;
  position: absolute;
  background-size: cover;
  box-shadow: 0 0 4px rgba( 0,0,0,0.3 );
  transition: all 0.25s;
}
</style>



